import styles from './about.module.css';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import { Fade } from 'react-awesome-reveal';
import { Fragment } from 'react';
import Heading from '../utils/Heading';
import ImageComp from '../utils/ImageComp';

import introductionBackground from './static/notebackground.webp';
import introductionBackgroundFallback from './static/notebackground.png';

import welcomeBackground from './static/welcomebackground.webp';
import welcomeBackgroundFallback from './static/welcomebackground.png';

const aboutUsText =
    `
Cm n bn  n vn  hc ca ti mnh. 
Nh mi khu vn, ngi lm vn u o bi nhm nui dng mm cy ca mnh. 
Di t cch l ngi lm vn, bn mnh khng ly g lm t ho hn khi c c c quyn trng v chm sc ht kin thc ti khu vn ny.
Nhng kin thc c nui trng vi nim yu qu mnh lit i vi Ngn ng, c bit l Ngn Ng Anh, phng tin mang mi min bin gii gn nhau hn bn cnh internet.
y s l ni bn mnh chia s tt tn tt v Ngn Ng Anh, t nhng bn tin nhanh ti phng php tip cn ngn ng.
Ti mnh hiu tt c chng ta u c trong lng mt s thi thc  hc v pht trin bn thn.
Nu bn  lc n y, hoc bn n y c chung mt mc ch,  hc, hiu v bt u mt chng ng hc thut.
Xin ng ngn ngi nhn tin cho bn t  ni ln nguyn vng ca bn.
Hai ngi bn t H v Trung, lun ch c bn ng hnh.
Khng dm v ngc t xng chng t s l ngi thy, c tuyt vi nht, nhng vi t cch l ngi hng dn cho cc bn, bn mnh s cng nhau i v gii quyt nhng rc ri ca ngn ng v khin chng ng tht s xng ng  tri nghim.
`;

function Welcome() {
    return (
        <div className={styles['welcome']}>

            <Fade className={styles['welcome-text']}>
                <Heading content="Chuyn Khu Vn" />
            </Fade>
            <Container maxWidth="xs">
                <ImageComp className={styles['welcome-picture']} image={{
                    url: welcomeBackground,
                    description: "welcome background",
                    fallback: welcomeBackgroundFallback,
                }} />
            </Container>

        </div>
    )
}

export default function Introduction() {
    return (
        <Fragment>
            <Welcome />
            <div className={styles['introduction']}>

                <ImageComp className={styles['introduction-background']} image={{
                    url: introductionBackground,
                    description: "note background",
                    fallback: introductionBackgroundFallback,
                }} />
                <Container sx={{
                    borderRadius: "10px",
                    position: "relative",
                    height: "100%",
                    backgroundColor: "var(--secondary)",
                }} maxWidth="md">

                    <Fade damping={0.1}>
                        <Typography lineHeight={"1.5em"} component="div" gutterBottom variant='introduction' textAlign={'justify'}>
                            {aboutUsText}
                        </Typography>
                    </Fade>

                    <Container sx={{
                        display: "flex",
                        justifyContent: "center",
                        height: "100%"
                    }} maxWidth="md">
                        <Fade damping={0.1}>
                            <Typography
                                component="div"
                                gutterBottom
                                variant='signature'
                                textAlign={'center'}>
                                H v Trung
                            </Typography>
                        </Fade>
                    </Container>


                </Container>

            </div>
        </Fragment>

    )
}